.ChangeWidthBlock {
  height: 100%;
  min-width: 0px;
  z-index: 1;
}
.ChangeWidth {
  position: absolute;
  top: 0px;
  background: grey;
  left: 50%;
  transform: translateX(-50%);
  height: 100%;
  padding: 0px 8px;
  cursor: col-resize;
  box-sizing: content-box;
  background-clip: content-box;
  opacity: 0;
  transition: 0.3s;
}
.ChangeWidth:hover {
  opacity: 0.5;
}
.ChangeWidth:active {
  opacity: 1;
  background: var(--BTN_BACK_GREEN);
  background-clip: content-box;
}
.ChangeWidthGreen {
  border-radius: 50%;
  width: 4px;
}
.ChangeWidthBlue {
  width: 3px;
  border-bottom-right-radius: 50%;
  border-top-right-radius: 50%;
}
.ChangeWidthBlue:active {
  background: var(--BLUE);
  background-clip: content-box;
}