.MainHomeProfileAboutMeBackLong {
  position: absolute;
  left: 100px;
  padding: 20px;
  padding-left: 130px;
  padding-top: 30px;
  bottom: -1px;
  min-width: min-content;
  height: 70%;
  background: var(--BACK_TONE_HEADER);
  width: calc(90% - 100px);
  border-radius: var(--BORDER_RADIUS_BLOCK);
  border: 1px solid var(--BORDER_COLOR_BLOCK);
  box-shadow: 0px 0px 20px 3px #000000e3;
  display: flex;
}
.MainHomeProfileAboutMe {
  position: absolute;
  top: 55%;
  left: 10%;
  width: 60%;
  height: 200px;
  display: flex;
}
.MainHomeProfileAboutMeFotoBlock {
  position: absolute;
  border-radius: 50%;
  min-width: 200px;
  height: 200px;
  background: darkolivegreen;
  border: 5px solid var(--BACK_TONE_HEADER);
  overflow: hidden;
}
.MainHomeProfileAboutMeFotoBlock:nth-child(1) {
  outline: 1px solid var(--BORDER_COLOR_BLOCK);
  box-shadow: 0px 0px 20px 3px #000000e3;
}
.OnlineStatusСircleBlock {
  overflow: visible;
  background: none;
  border: none;
}
.MainHomeProfileAboutMeFotoBlock img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 100%;
}
.MainHomeProfileAboutMeDataBlock {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.MainHomeProfileAboutMeName {
  position: relative;
  font-weight: 900;
  font-size: 150%;
  color: #c9d1d9;
  white-space: nowrap;
}
.MainHomeProfileAboutMeStatus {
  position: relative;
  margin-top: 10px;
  opacity: 0.6;
  color: #c9d1d9;
  font-size: 80%;
  display: flex;
  align-items: center;
  cursor: pointer;
  white-space: nowrap;
}
.peopleStatus {
  cursor: default;
}
.MainHomeProfileAboutMeStatus button {
  position: absolute;
  left: calc(100% + 5px);
  top: 60%;
  transform: translateY(-50%);
  background: none;
  border: none;
  padding: 0px;
}
.MainHomeProfileAboutMeStatus button img {
  height: 15px;
}
.MainHomeProfileAboutMeStatus button:hover {
  opacity: 0.7;
}
.MainHomeProfileAboutMeButtonFollowed {
  margin-left: 30px;
  display: flex;
}
.MainHomeProfileAboutMeStatusEditBlock {
  position: relative;
  width: 10px;
  margin-left: 5px;
}
.MainHomeProfileAboutMeStatusEditBlock img{
  position: relative;
  width: 100%;
}
.StatusInput {
  position: relative;
  background: var(--BACK_TONE_MAIN_TWO);
  font-size: 100%;
  padding: 0px;
  border: 1px solid var(--BORDER_COLOR_BLOCK);
  border-radius: var(--BORDER_RADIUS_BLOCK);
  margin: 0px;
  padding: 5px 10px;
  width: 100%;
}
.StatusInput:focus {
  border: 1px solid var(--BORDER_COLOR_BLOCK);
  border-radius: var(--BORDER_RADIUS_BLOCK);
  outline: none;
}
.addStatus {
  opacity: 0.5;
}