body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
:root {
  --BACK_TONE_MAIN_ONE: #010409;
  --BACK_TONE_MAIN_TWO: #0d1117;
  --BTN_BACK_DEFAULT: #21262d;
  --BTN_BACK_DEFAULT_HOVER: #30363d;
  --BTN_BACK_GREEN: #238636;
  --BACK_TONE_HEADER: #161b22;
  --BORDER_RADIUS_BLOCK: 6px;
  --BORDER_COLOR_BLOCK: #21262d;
  --BORDER_COLOR_BLOCK_HOVER: #8b949e;
  --COLOR_GREY: #8b949e;
  --BLUE: #58a6ff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
a {
  text-decoration: none;
}
button {
  background: var(--BTN_BACK_DEFAULT);
  border-radius: var(--BORDER_RADIUS_BLOCK);
  border: 1px solid var(--BORDER_COLOR_BLOCK);
  padding: 3px 12px;
  line-height: 20px;
  font-size: 12px;
  cursor: pointer;
  transition: 0.3s;
  font-weight: 600;
  white-space: nowrap;
}
button:hover {
  background: var(--BTN_BACK_DEFAULT_HOVER);
  border: 1px solid var(--BORDER_COLOR_BLOCK_HOVER);
  color: #c9d1d9;
}
.BtnGreen {
  background: var(--BTN_BACK_GREEN);
  color: #ffffff;
  border: none;
}
.BtnGreen:disabled {
  opacity: 0.4;
}
.BtnGreen:hover {
  background: #2ea043;
  border: none;
  color: #ffffff;
}
input:-webkit-autofill {
  /*background-color: green;*/
  /*color: red;*/
  /*border: 3px solid blue;*/
}
input:-webkit-autofill {
  /*background-color: red;*/
  /*color: red;*/
  /*border: 3px solid blue;*/
}
input {
  color-scheme: dark;
}
.label {
  position: relative;
  width: 100%;
  height: auto;
}
.label span {
  margin: 0px;
  position: relative;
  font-size: 60%;
  margin-left: 5px;
  color: red;
}
input {
  width: 100%;
}

* {
  -ms-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  margin: 0px;
  box-sizing: border-box;
  color: #c9d1d9;
  font-family: "Facundo", monospace;
}
*::-webkit-scrollbar {
  width: 10px;
}
*::-webkit-scrollbar-track {
  border-bottom-right-radius: var(--BORDER_RADIUS_BLOCK);
  border-top-right-radius: var(--BORDER_RADIUS_BLOCK);
  box-shadow: inset 0px 0px 0px 0px #000;
  background: #4040402b;
}
*::-webkit-scrollbar-track:hover {
  background: #404040;
  box-shadow: inset 0px 0px 4px 2px #0000004f;
}
*::-webkit-scrollbar-thumb {
  background: #dbdbdb;
  border-radius: 20px;
  border: 3px solid #00000000;
  background-clip: content-box;
}
*::-webkit-scrollbar-thumb:hover {
  background: white;
  background-clip: content-box;
}
.ScrollbarLittle::-webkit-scrollbar {
  width: 2px;
}