.form-sign-input {
  position: relative;
  background: none;
  outline: none;
  border: 1px solid var(--BORDER_COLOR_BLOCK);
  border-radius: var(--BORDER_RADIUS_BLOCK);
  height: 45px;
  padding: 0px 20px;
}
.inputRedBorder {
  border: 1px solid red;
}
.checkbox-auth-item-none {
  position: absolute;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  height: 100%;
  opacity: 0;
  cursor: pointer;
  z-index: 2;
}
.checkbox-auth-block {
  display: block;
  position: relative;
  margin: 0px 0px;
  margin-bottom: 7px;
  width: max-content;
  left: 5px;
}
.checkbox-auth-span {
  position: relative;
  margin-left: 0px;
  display: flex;
  width: max-content;
  font-size: 70%;
  align-items: center;
  cursor: pointer;
  transition: 0.2s;
  opacity: 0.7;
}
.checkbox-auth-item-block {
  transition: 0.2s;
  position: relative;
  width: 20px;
  height: 20px;
  border-radius: var(--BORDER_RADIUS_BLOCK);
  border: 1px solid var(--BORDER_COLOR_BLOCK);
  margin-right: 10px;
}
.checkbox-auth-name{
  white-space: nowrap;
  transition: 0.2s;
  color: var(--COLOR_GREY);
}
.checkbox-auth-item-1-1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70%;
  height: 70%;
  border-radius: 4px;
  background: var(--BLUE);
  overflow: hidden;
  opacity: 0;
  transition: 0.2s;
}
.checkbox-auth-item-none:checked + .checkbox-auth-span .checkbox-auth-item-block {
  border: 1px solid var(--BORDER_COLOR_BLOCK_HOVER);
}
.checkbox-auth-item-none:checked + .checkbox-auth-span .checkbox-auth-name {
  color: white;
}
.checkbox-auth-block:hover .checkbox-auth-span .checkbox-auth-item-block {
  border: 1px solid var(--BORDER_COLOR_BLOCK_HOVER);
}
.checkbox-auth-block:hover .checkbox-auth-span .checkbox-auth-name {
  color: white;
}
.checkbox-auth-item-none:checked + .checkbox-auth-span .checkbox-auth-item-block .checkbox-auth-item-1-1 {
  overflow: visible;
  opacity: 1;
}


.eye-password {
  position: absolute;
  right: 15px;
  top: 13px;
  opacity: 0.3;
  cursor: pointer;
  transition: 0.2s;
}
.eye-password:hover {
  opacity: 0.5;
}
.eye-password-long {
  position: absolute;
  width: 120%;
  height: 1px;
  background: var(--COLOR_GREY);
  top: 46%;
  left: 60%;
  border-radius: var(--BORDER_RADIUS_BLOCK);
  outline: 2px solid var(--BACK_TONE_MAIN_TWO);
  transform: translate(-50%, -50%) rotate(-45deg);
}
#eye {
  display: inline-block;
  position: relative;
  width: 1.5em; /* ширина элемента */
  height: 1.5em; /* высота элемента */
  background: var(--COLOR_GREY); /* цвет иконки как цвет текста */
  border-radius: 70% 0 / 70%;
  transform: rotate(45deg);
}
#eye::before,
#eye::after {
  content: "";
  position: absolute;
  top: 20%;
  left: 20%;
  width: 60%;
  height: 60%;
  background: var(--BACK_TONE_MAIN_TWO);;
  border-radius: 100%;
}
#eye::after {
  width: 30%;
  height: 30%;
  top: 35%;
  left: 35%;
  background: inherit; /* цвет зрачка как у родителя */
}
.captcha-block {
  display: flex;
  position: relative;
  border-radius: var(--BORDER_RADIUS_BLOCK);
  overflow: hidden;
  height: 60px;
  border: 1px solid var(--BORDER_COLOR_BLOCK);
}
.captcha-img {
  position: relative;
  height: 100%;
  width: inherit;
  overflow: hidden;
}
.captcha-img img {
  opacity: 0.8;
  position: relative;
  width: inherit;
  height: 100%;
}
.captcha-input {
  width: 60%;
  position: relative;
  background: none;
  outline: none;
  border: none;
  padding: 0px 20px;
  font-size: 120%;
  text-align: center;
}























