.Settings {
  position: relative;
  background: var(--BACK_TONE_MAIN_ONE);
  padding: 20px;
}
.SettingsAllDataBlock {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 30px;
  padding-bottom: 10px;
  display: flex;
  flex-direction: column;
  width: min-content;
  border: 1px solid var(--BORDER_COLOR_BLOCK);
  border-radius: var(--BORDER_RADIUS_BLOCK);
  background: var(--BACK_TONE_MAIN_TWO);
}
.SettingsAllDataBlockTwo {
  position: relative;
  display: flex;
}
.SettingsAllDataPhoto {
  position: relative;
  width: 200px;
  height: 200px;
  overflow: hidden;
  border-radius: 50%;
}
.lol {
  opacity: 0;
  position: absolute;
  z-index: -1;
}
.SettingsAllDataPhoto img{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
}
.SettingsAllDataPhoto-download {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #0000008f;
  width: 100%;
  cursor: pointer;
  height: 100%;
  transition: 0.3s;
}
.SettingsAllDataPhoto-download:hover {
  background: #7a7a7a8c;
}
.SettingsAllDataPhoto-download-cross {
  position: absolute;
  top: 45%;
  left: 50%;
  width: 15%;
  height: 3px;
  background: #c5c5c561;
  border-radius: 50px;
  transition: 0.3s;
}
.mam1 {
  transform: translate(-50%, -50%) rotate(90deg);
}
.mam2 {
  transform: translate(-50%, -50%) rotate(0deg);
}
.SettingsAllDataPhoto-download:hover .SettingsAllDataPhoto-download-cross{
  background: #191919;
}
.SettingsAllDataPhoto-text {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 60%;
  left: 50%;
  white-space: nowrap;
  font-weight: 800;
  opacity: 0.6;
  font-size: 80%;
  transition: 0.3s;
}
.SettingsAllDataPhoto-download:hover .SettingsAllDataPhoto-text{
  color: #191919;
}
.SettingsAllDataInfo {
  margin-left: 50px;
  position: relative;
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  height: min-content;
  align-self: center;
}
.SettingsAllDataInfo span {
  position: relative;
  font-weight: 900;
  font-size: 110%;
  opacity: 0.8;
}
.SettingsAllDataInfo-block-item {
  position: relative;
}
.SettingsAllData {
  position: relative;
  font-size: 80%;
}
.Settings-btn {
  position: relative;
  margin-top: 20px;
  margin-bottom: 10px;
  width: min-content;
  align-self: end;
}
.SettingsAllDataBlock-Preloader {
  position: absolute;
  width: min-content;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}