.notifications-errors {
  position: relative;
  width: 100%;
  border-radius: var(--BORDER_RADIUS_BLOCK);
  border: 1px solid #ed6868;
  padding: 5px;
  background: #6b0101e0;
  color: var(--COLOR_GREY);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  padding: 10px;
  font-size: 110%;
}
.content-error {

}
.name-error {
  color: rgba(255, 255, 255, 0.3);
}
.button-notifications-update {
  border: 1px solid rgba(255, 255, 255, 0.39);
  background: none;
}
.cross-notifications-block {
  position: absolute;
  width: 15px;
  height: 15px;
  top: 7px;
  right: 7px;
  cursor: pointer;
}
.cross-notifications-block span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 120%;
  height: 1px;
  background: grey;
  border-radius: 50%;
  transition: 0.3s;
}
.cross-notifications-block span:nth-child(1) {
  transform: translate(-50%, -50%) rotate(45deg);
}
.cross-notifications-block span:nth-child(2) {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.cross-notifications-block:hover span {
  background: white;
}