.HeaderSearch {
  position: relative;
  display: flex;
  grid-gap: 10px;
  padding: 0px 15px;
  border-radius: var(--BORDER_RADIUS_BLOCK);
}
.HeaderSearchStandard {
  width: 100%;
  height: 100%;
}
.HeaderSearch img {
  position: relative;
  align-self: center;
  transform: rotate(-90deg);
  height: 40%;
  opacity: 0.4;
}
.HeaderSearch input{
  position: relative;
  width: 100%;
  height: 100%;
  outline: none;
  border: none;
  background: none;
  font-size: 90%;
}