.notifications-wrapper {
  position: fixed;
  right: 30px;
  bottom: 20px;
  width: 450px;
  height: max-content;
  display: flex;
  flex-direction: column;
  grid-gap: 5px;
  font-size: 90%;
}
.notifications-close-all {
  position: relative;
  margin-right: 0px;
  margin-left: auto;
  padding: 5px;
  background: var(--BTN_BACK_DEFAULT);
  text-align: center;
  opacity: 0;
  cursor: pointer;
  width: 50%;
  border-radius: var(--BORDER_RADIUS_BLOCK);
}
.notifications-wrapper:hover .notifications-close-all {
  opacity: 0.3;
}
.notifications-close-all:hover {
  opacity: 0.5;
}