.MainHomeProfileLookingForAJobBlock {
  position: relative;
  display: flex;
  padding: 10px;
  border-radius: var(--BORDER_RADIUS_BLOCK);
  border: 1px solid var(--BORDER_COLOR_BLOCK);
  background: none;
  width: max-content;
  max-width: 100%;
}
.MainHomeProfileLookingForAJobIconBlock {
  position: relative;
  min-width: 50px;
  min-height: 50px;
  width: 50px;
  height: 50px;
}
.MainHomeProfileLookingForAJobIconBlock img {
  position: relative;
  width: 100%;
}
.MainHomeProfileLookingForAJobDataBlock {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  padding: 0px 10px;
}
.MainHomeProfileLookingForAJob span{
  opacity: 0.5;
  white-space: nowrap;
  font-size: 90%;
  font-weight: 800;
}