.MessagesChatBlock {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top-right-radius: var(--BORDER_RADIUS_BLOCK);
  border-bottom-right-radius: var(--BORDER_RADIUS_BLOCK);
  background: var(--BACK_TONE_MAIN_TWO);
  border-left: 0.5px solid var(--BORDER_COLOR_BLOCK);
}