.MessagesSelectSearch {
  position: relative;
  width: 100%;
  display: flex;
  justify-items: center;
  padding: 7px 10px;
  transition: 0.3s;
}
.MessagesSelectSearch-photo {
  position: relative;
  border-radius: 50%;
  width: 50px;
  height: 50px;
}
.MessagesSelectSearch-name {
  position: relative;
  display: block;
  justify-self: center;
  align-self: center;
  margin-left: 20px;
  font-weight: 800;
}
.MessagesSelectSearch:hover {
  background: var(--BORDER_COLOR_BLOCK);
}