.MessagesSelectBlock {
  position: relative;
  width: 30%;
  height: 100%;
  border-top-left-radius: var(--BORDER_RADIUS_BLOCK);
  border-bottom-left-radius: var(--BORDER_RADIUS_BLOCK);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.MessagesSelectHeader {
  border-bottom: 1px solid var(--BORDER_COLOR_BLOCK);
  width: 100%;
  height: 60px;
  background: var(--BACK_TONE_MAIN_TWO);
}