.MainHomeUsersItem {
  position: relative;
  width: 100%;
}
.MainHomeUsersItemPhotoBlock {
  position: relative;
  display: flex;
  border-radius: var(--BORDER_RADIUS_BLOCK);
  width: 100%;
  overflow: hidden;
}
.MainHomeUsersItemPhotoBack {
  position: relative;
  opacity: 0;
  width: 100%;
}
.MainHomeUsersItemPhotoMain {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}
.MainHomeUsersItemName {
  word-break: break-all;
  margin-top: 10px;
  font-weight: 900;
}
.MainHomeUsersItemBtn {
  position: relative;
  margin-top: 10px;
}