.MessagesChatMain-wrapper {
  position: relative;
  flex: 1 1 auto;
  padding: 0px 0px;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.MessagesChatMain-wrapper-circle {
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  /*position: absolute;*/
}
.renderItemMessages-wrapper {
  padding: 5px 30px;
  position: relative;
  width: 100%;
  cursor: pointer;
}
.MessagesChatMain {
  position: relative;
  width: 100%;
  height: max-content;
  margin-bottom: 0px;
  margin-top: auto;
  padding: 30px 0px;
  padding-top: 50px;
}
.renderItemMessages-block span {
  opacity: 0.5;
  font-size: 60%;
  font-style: italic;
  align-self: end;
  margin-left: 10px;
  height: min-content;
  min-width: max-content;
}
.renderItemMessages-block textarea {
  background: none;
  outline: none;
  border: none;
  resize: none;
  height: min-content;
}
.renderItemMessages-block {
  cursor: pointer;
  background: var(--BTN_BACK_DEFAULT_HOVER);
  position: relative;
  display: flex;
  margin: 0px 0px;
  padding: 5px 7px;
  max-width: 500px;
  word-break:break-all;
}
.itemMesСompanion {
  width: max-content;
  margin-left: 0px;
  margin-right: auto;
  border-bottom-left-radius: 0px;
  border-radius: var(--BORDER_RADIUS_BLOCK);
  border-bottom-left-radius: 0px;
  background: var(--BORDER_COLOR_BLOCK);
}
.itemMesMy {
  margin-left: auto;
  margin-right: 0px;
  width: max-content;
  border-radius: var(--BORDER_RADIUS_BLOCK);
  border-bottom-right-radius: 0px;
}

