.MainHomeProfile {
  overflow: hidden;
  overflow-y: scroll;
}
.MainHomeProfileHeader {
  position: relative;
  width: 100%;
  height: 300px;
  background: linear-gradient(rgb(1, 4, 9), #ffffff33);
}
/*MAIN*/
.MainHomeProfileMain {
  padding:  0px 10%;
  position: relative;
  width: 100%;
  margin-top: 130px;
}
.btnExitProfile {
  position: absolute;
  left: 0px;
  top: 0px;
  opacity: 0.1;
  width: 150px;
  height: 100px;
  background: var(--COLOR_GREY);
  z-index: 3;
  cursor: pointer;
  border-bottom-right-radius: var(--BORDER_RADIUS_BLOCK);
}
.btnExitProfile:hover {
  opacity: 0.3;
  transition: 0.3s;
}


.arrow-7 {
  position: absolute;
  top: 50%;
  left: 35%;
  width: 38px;
  height: 100px;
  transform: translate(-50%, -50%) rotate(90deg);
}
.arrow-7 span {
  display: block;
  width: 30px;
  height: 30px;
  border-bottom: 5px solid var(--BACK_TONE_MAIN_ONE);
  border-right: 5px solid var(--BACK_TONE_MAIN_ONE);
  transform: rotate(45deg);
  margin: -16px 0;
  animation: arrow-7 2s infinite;
}
.arrow-7 span:nth-child(2){
  animation-delay: -0.2s;
}
.arrow-7 span:nth-child(3){
  animation-delay: -0.4s;
}
@keyframes arrow-7 {
  0%{
    opacity: 0;
    transform: translate(0,-20px) rotate(45deg);
  }
  50%{
    opacity: 1;
  }
  100%{
    opacity: 0;
    transform: translate(0,20px) rotate(45deg);
  }
}

