.MainHomeUsers {
  border-radius: var(--BORDER_RADIUS_BLOCK);
  border: 1px solid var(--BORDER_COLOR_BLOCK);
  background: var(--BACK_TONE_MAIN_ONE);
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
}
.showMore {
  display: flex;
  flex-direction: column;
  align-self: center;
  align-items: center;
  margin-bottom: auto;
  margin-top: auto;
  padding-top: 50px;
  padding-bottom: 40px;
}
.MainHomeUsersBlock {
  padding: 0px 5%;
  position: relative;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-row-gap: 40px;
  grid-column-gap: 30px;
  justify-items: center;
}
