/*    grid-template-columns: repeat(4, 1fr);*/
/*  }*/
/*}*/
@media (max-width: 1230px) {
  .MainHomeUsersBlock {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (max-width: 980px) {
  .MainHomeUsersBlock {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 850px) {
  .MainHomeUsersBlock {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 600px) {
  .MainHomeUsersBlock {
    grid-template-columns: repeat(1, 1fr);
  }
}
.users-search {
  border: 1px solid var(--BORDER_COLOR_BLOCK);
  max-width: 300px;
  height: 50px;
}
.not-users {
  position: relative;
}
.not-users span{
  position: relative;
  font-weight: 900;
}

.totalCount {
  position: relative;
  margin-right: 0px;
  margin-left: auto;
  display: flex;
  align-items: center;
  font-weight: 900;
}
.totalCount p {
  font-weight: 400;
  margin-right: 10px;
}
.totalCount div {
  font-weight: 400;
}
.users-search-block {
  position: sticky;
  top: 0px;
  width: 100%;
  margin: 20px 0px;
  z-index: 2;
  background: var(--BACK_TONE_MAIN_ONE);
  padding: 10px 5%;
  display: flex;
}

/*radio*/
.header-users-radio-block {
  position: relative;
  padding: 0px 5%;
  background: #161b22;
  border-bottom: 1px solid var(--BORDER_COLOR_BLOCK);
}
.radio-buttons-block {
  position: relative;
  display: flex;
}
.header-radio-item {
  position: relative;
  font-weight: 900;
  font-size: 110%;
  cursor: pointer;
  border-bottom: 2px solid transparent;
  padding: 15px 15px;
  white-space: nowrap;
  color: #c9d1d9;
  transition: 0.3s;
}
.header-radio-item:hover {
  border-bottom: 2px solid var(--BTN_BACK_DEFAULT_HOVER);
}
.header-radio-item-active {
  color: rgba(192, 200, 210, 0.62);
}
.radio-long {
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0px;
  background: var(--BLUE);
  transition: 0.3s;
}
.Found {
  position: relative;
  display: flex;
  align-items: center;
  margin-left: 30px;
}
.Found p{
  text-transform: uppercase;
  font-weight: 800;
  margin-right: 10px;
}
.Found span{
  opacity: 0.8;
}




