.MessagesSelectMain_header-block {
  position: relative;
  width: 100%;
  min-height: 70px;
  margin-top: 10px;
  padding: 0px 10px;
}
.swiper-wrapper {
  position: relative;
  width: 0px;
}
.swiper-slide {
  position: relative;
  width: 60px;
  min-height: 70px;
  text-align: center;
  white-space: nowrap; /* Запрещаем перенос строк */
  overflow: hidden; /* Обрезаем все, что не помещается в область */
  text-overflow: ellipsis;
}
.long-MessagesSelectMainHeaderItem {
  position: relative;
  width: 100%;
  min-height: 10px;
  background: var(--BTN_BACK_DEFAULT);
  margin-top: 10px;
  margin-bottom: 20px;
}