.MainHomeProfileContactsBlock {
  margin-top: 50px;
  position: relative;
  display: flex;
  flex-direction: column;
  width: min-content;
  border-radius: var(--BORDER_RADIUS_BLOCK);
  border: 1px solid var(--BORDER_COLOR_BLOCK);
  overflow: hidden;
}
.MainHomeProfileContactsHeader {
  position: relative;
  text-align: center;
  text-transform: uppercase;
  width: 100%;
  padding: 10px;
  color: var(--BLUE);
  font-size: 80%;
  white-space: nowrap;
  font-weight: 800;
  background: var(--BACK_TONE_HEADER);
  border-bottom: 1px solid var(--BORDER_COLOR_BLOCK);
}
.MainHomeProfileContactsMain {
  padding: 15px;
  position: relative;
  display: flex;
  grid-gap: 10px;
  justify-content: center;
}
.MainHomeProfileContactsItem {
  position: relative;
  min-width: 40px;
  width: 40px;
  height: 40px;
  cursor: pointer;
  transition: 0.3s;
}
.MainHomeProfileContactsItem:hover {
  opacity: 0.5;
}
.MainHomeProfileContactsItem img {
  position: relative;
  width: 100%;
}