.WriteMessage-btn {
  position: relative;
  height: min-content;
  margin-right: 20px;
}
.WriteMessage-alert-block {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  background: #000000bf;
  z-index: 4;
}
.WriteMessage-alert {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 400px;
  background: var(--BACK_TONE_MAIN_TWO);
  border-radius: var(--BORDER_RADIUS_BLOCK);
  border: 1px solid var(--BORDER_COLOR_BLOCK);
}
.WriteMessage-alert-header {
  background: var(--BACK_TONE_HEADER);
  border-bottom: 1px solid var(--BORDER_COLOR_BLOCK);
  padding: 20px 20px;
  text-align: center;
  font-size: 110%;
  text-transform: uppercase;
  font-weight: 900;
}
.WriteMessage-alert-main {
  position: relative;
  padding: 20px;
}
.WriteMessage-alert-main img{
  position: relative;
  min-width: 35px;
  height: 35px;
  border-radius: 50%;
  overflow: hidden;
}
.WriteMessage-alert-main-data {
  position: relative;
  display: flex;
  font-weight: 600;
  opacity: 0.8;
  font-size: 80%;
  align-items: center;
  grid-gap: 10px;
}
.WriteMessage-alert-main-form {
  position: relative;
  margin-top: 20px;
}
.WriteMessage-alert-main-input {
  position: relative;
  width: 100%;
  height: 40px;
  background: none;
  border: 1px solid var(--BORDER_COLOR_BLOCK);
  outline: none;
  border-radius: var(--BORDER_RADIUS_BLOCK);
  padding: 10px;
}
.WriteMessage-alert-main-form-btns {
  grid-gap: 10px;
  position: relative;
  margin-top: 15px;
  display: flex;
  width: 100%;
}
.WriteMessage-alert-main-form-btns button{
  position: relative;
  padding: 7px 0px;
  width: 100%;
}