.notifications-information {
  position: relative;
  width: 100%;
  border-radius: var(--BORDER_RADIUS_BLOCK);
  border: 1px solid var(--BORDER_COLOR_BLOCK);
  color: var(--COLOR_GREY);
  background: var(--BACK_TONE_HEADER);
  padding: 20px 20px;
  padding-top: 10px;
  padding-right: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.content-information {
  word-wrap: break-word;
}
.name-information {
  margin-bottom: 10px;
  color: rgba(255, 255, 255, 0.3);
}