.NavHomePoint {
  position: relative;
  padding: 7px;
  padding-left: 30px;
  font-size: 120%;
  font-weight: 900;
}
.NavHomePointBottom {
  position: relative;
  margin-right: auto;
  margin-left: 30px;
  margin-bottom: 30px;
  margin-top: auto;
}
.NavHomePointsBlock p {
  opacity: 0.5;
  transition: 0.3s;
}
.NavHomePoint:hover p {
  transform: translateX(5px);
}
.NavHomePointsBlock .active p {
  opacity: 1;
  transform: translateX(15px);
}

