.GrayBoot {
  position: relative;
  width: 100%;
  height: 100%;
  background: grey;
  border-radius: var(--BORDER_RADIUS_BLOCK);
  overflow: hidden;
}
.GrayBootItem {
  position: absolute;
  width: 0px;
  height: 150%;
  left: 50%;
  top: 50%;
  background: #777c85;
  transform: translateY(-50%) rotate(30deg);
  box-shadow: 0px 0px 9px 5px #ffffffc2;
  animation-name: GrayBootItem;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}
@keyframes GrayBootItem {
  0%{left: -50%;}
  100%{left: 150%;}
}