.MessagesChatHeader {
  min-height: 60px;
  padding: 0px 30px;
  display: flex;
  border-bottom: 1px solid var(--BORDER_COLOR_BLOCK);
  align-items: center;
}
.MessagesChatHeader-name {
  position: relative;
  width: max-content;
  font-weight: 900;
  font-size: 110%;
  transition: 0.3s;
}
.MessagesChatHeader-name:hover {
  opacity: 0.7;
}
.MessagesChatHeader-photo-block {
  position: relative;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 0px;
  margin-left: auto;
  transition: 0.3s;
}
.MessagesChatHeader-photo-block:hover {
  opacity: 0.7;
}
.MessagesChatHeader-photo-block img {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}