.MessagesSelectItem {
  position: relative;
  width: 100%;
  padding: 7px 10px;
  display: flex;
  cursor: pointer;
  transition: 0.3s;
}
.MessagesSelectItem:hover {
  background: var(--BORDER_COLOR_BLOCK);
}
.MessagesSelectItemBlockPhoto {
  position: relative;
  border-radius: 50%;
  min-width: 50px;
  min-height: 50px;
  background: grey;
  border-radius: 50%;
  overflow: hidden;
}
.MessagesSelectItemBlockPhoto img {
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.MessagesSelectItemBlockData {
  position: relative;
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  margin-left: 10px;
  overflow: hidden;
}
.MessagesSelectItemNameTimeBlock {
  position: relative;
  display: flex;
}
.MessagesSelectItemName {
  position: relative;
  font-weight: 800;
}
.MessagesSelectItemTime {
  font-size: 90%;
  position: relative;
  margin-right: 0px;
  margin-left: auto;
}
.MessagesSelectItemLastMessage {
  position: relative;
  font-size: 80%;
  margin-top: 5px;
  color: var(--COLOR_GREY);
  display: flex;
}
.MessagesSelectItemLastMessage-new{
  margin-right: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
  margin-left: auto;
  font-size: 60%;
  min-width: 15px;
  height: 15px;
  color: white;
  padding: 0px 5px;
  font-weight: 900;
  background: var(--BTN_BACK_GREEN);
}