.login-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: linear-gradient(149deg, var(--BACK_TONE_MAIN_ONE) 0%,
  #150b1c 42%,
  #1a0414 72%,
  #0c0c34 100%);
  animation: gradient 20s infinite linear;
  background-size: 400%;
}
.login-wrapper::before {
  content: '';
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background: #0f172954;
}
@keyframes gradient {
  0% {
    background-position: 80% 0%;
  }
  50% {
    background-position: 20% 100%;
  }
  100% {
    background-position: 80% 0%;
  }
}
.form-wrapper {
  position: relative;
  width: 95%;
  max-width: 370px;
  background: var(--BACK_TONE_MAIN_TWO);
  border-radius: var(--BORDER_RADIUS_BLOCK);
  border: 1px solid var(--BORDER_COLOR_BLOCK);
  display: flex;
  padding: 0px 30px;
  flex-direction: column;
}
.form-name {
  position: relative;
  text-align: center;
  margin: 20px 0px;
  font-family: sans-serif;
  font-weight: 900;
}
.form-redirect-text {
  position: relative;
  padding: 20px 0px;
  margin-top: auto;
  font-size: 80%;
  text-align: center;
}
.form-redirect-text a {
  position: relative;
  text-decoration: underline;
  color: var(--BLUE);
}
.form-redirect-text a:hover {
  text-decoration: auto;
}
.form-sign {
  position: relative;
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
}
.form-sign button {
  position: relative;
  height: 40px;
  font-size: 90%;
}