.NavHome {
  position: relative;
  height: 100%;
  box-shadow: 0px 0px 10px 0px #00000085;
  display: flex;
  flex-direction: column;
  background: var(--BACK_TONE_MAIN_TWO);
}
.NavHomeHeaderBlock {
  position: relative;
  width: 100%;
  padding: 5px;
}
.NavHomeHeader {
  position: relative;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  text-align: center;
  padding: 15px;
  font-size: 140%;
  font-weight: 900;
  background: var(--BACK_TONE_MAIN_ONE);
  color: #58a6ff;
  transition: 0.3s;
  transition: width 0s;
  cursor: pointer;
  border-radius: var(--BORDER_RADIUS_BLOCK);
  border: 1px solid var(--BORDER_COLOR_BLOCK);
}
.NavHomeHeader:hover {
  transition: width 0s, height 0s;
  opacity: 0.7;
}
.NavHomePointsBlock {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  transition: 0.3s;
  margin-top: 20px;
}
.btnSettings {
  position: relative;
  font-weight: 700;
  font-size: 100%;
  display: flex;
  grid-gap: 5px;
}
.NavHomePointsBlock .active .navHomeTypeIcon_2_BLOCK {
  opacity: 1;
}
.navHomeTypeIcon_2_BLOCK {
  position: relative;
  width: 50px;
  height: 50px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0.3;
  transition: opacity 0.3s;
}
.navHomeTypeIcon_2_BLOCK:hover {
  opacity: 0.6;
}
.navHomeTypeIcon_2_BLOCK img {
  position: relative;
  width: 60%;
}
.NavHomeHeader_2 {
  opacity: 1;
}
.NavHomeHeader_2 img {
  animation: NavHomeHeader_2ImgRotate 75s infinite linear;
  width: 80%;
}
.NavHomeHeader_2:hover {
  opacity: 1;
}
@keyframes NavHomeHeader_2ImgRotate {
  0% { transform: rotate(0deg) scale(1);}
  50% {transform: rotate(180deg) scale(0.7);}
  100% { transform: rotate(360deg) scale(1);}
}




