.Preloader {
  position: relative;
  height: min-content;
}
/*circle*/
@keyframes TYPE_1_1 {
  0% { transform: rotate(0deg) }
  50% { transform: rotate(180deg) }
  100% { transform: rotate(360deg) }
}
.TYPE_1_1 div {
  position: absolute;
  animation: TYPE_1_1 1.12s linear infinite;
  width: 92px;
  height: 92px;
  top: 4px;
  left: 4px;
  border-radius: 50%;
  box-shadow: 0 2.3px 0 0 #c9d1d9;
  transform-origin: 46px 47.15px;
}
.TYPE_1_0 {
  width: 26px;
  height: 26px;
  display: inline-block;
  overflow: hidden;
  background: none;
}
.TYPE_1_1 {
  width: 100%;
  height: 100%;
  position: relative;
  transform: translateZ(0) scale(0.26);
  backface-visibility: hidden;
  transform-origin: 0 0; /* see note above */
}
.TYPE_1_1 div { box-sizing: content-box; }

