.MessagesSelectMainHeaderItem {
  position: relative;
  width: 100%;
  max-height: 100%;
  transition: 0.3s;
}
.MessagesSelectMainHeaderItem img{
  position: relative;
  border-radius: 50%;
  overflow: hidden;
  width: 80%;
}
.MessagesSelectMainHeaderItem-name {
  position: relative;
  font-size: 50%;
}
.MessagesSelectMain_header-block {
}
.swiper-button-prev {
  left: 10px;
  top: 60%;
  position: absolute;
  width: 25px;
  height: 25px;
  background: var(--BORDER_COLOR_BLOCK_HOVER);
  border-radius: 50%;
  border: 1px solid var(--BORDER_COLOR_BLOCK);
  box-shadow: 0px 0px 20px 5px #00000099;
 }
.swiper-button-next {
  right: 10px;
  top: 60%;
  position: absolute;
  width: 25px;
  height: 25px;
  background: var(--BORDER_COLOR_BLOCK_HOVER);
  border-radius: 50%;
  border: 1px solid var(--BORDER_COLOR_BLOCK);
  box-shadow: 0px 0px 20px 5px #00000099;
}
.swiper-button-prev::after {
   content: '';
   position: absolute;
   height: 70%;
   width: 70%;
  background: var(--BACK_TONE_MAIN_ONE);
  border-radius: 50%;
}
.swiper-button-next::after {
  content: '';
  position: absolute;
  height: 70%;
  width: 70%;
  background: var(--BACK_TONE_MAIN_ONE);
  border-radius: 50%;
}
.MessagesSelectMainHeaderItem:hover {
  opacity: 0.4;
}