.confirmation-wrapper {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(0deg) scale(1.1);
  width: 150vw;
  height: 150vh;
  background: #000000bf;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}
.confirmation {
  overflow: hidden;
  position: relative;
  background: var(--BACK_TONE_MAIN_TWO);
  border-radius: var(--BORDER_RADIUS_BLOCK);
  border: 1px solid var(--BORDER_COLOR_BLOCK);
}
.confirmationHeader {
  text-align: center;
  position: relative;
  padding: 15px 0px;
  font-size: 110%;
  font-weight: 900;
  border-bottom: 1px solid var(--BORDER_COLOR_BLOCK);
  background: var(--BACK_TONE_HEADER);
}
.confirmationMain {
  position: relative;
  padding: 25px 40px;
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
}
.confirmationMain p {
  font-weight: 500;
  font-size: 100%;
  padding: 10px 0px;
  text-align: center;
  color: #8b949e;
}
.confirmatioBtnBlock {
  position: relative;
  display: flex;
  grid-gap: 50px;
}
.confirmatioBtnBlock button:nth-child(1) {
  margin-left: 0px;
  min-width: 80px;
  margin-right: auto;
}
.confirmatioBtnBlock button:nth-child(2) {
  margin-left: auto;
  min-width: 80px;
  margin-right: 0px;
}
.confirmationTransition-enter {
  opacity: 0;
}
.confirmationTransition-enter-active {
  animation: shake 0.5s;
  opacity: 1;
  transition:  opacity 0.3s;
}
.confirmationTransition-exit {
  opacity: 1;
}
.confirmationTransition-exit-active {
  opacity: 0;
  transition: opacity 0.2s;
}
@keyframes shake {
  0%, 50%, 100% {
    transform: translate(-50%, -50%) rotate(0deg);
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    -moz-transform: translate(-50%, -50%) rotate(0deg);
    -o-transform: translate(-50%, -50%) rotate(0deg);
    -ms-transform: translate(-50%, -50%) rotate(0deg);
  }
  10%, 30% {
    transform:translate(-50%, -50%) rotate(-10deg);
    -webkit-transform: translate(-50%, -50%) rotate(-10deg);
    -moz-transform: translate(-50%, -50%) rotate(-10deg);
    -o-transform: translate(-50%, -50%) rotate(-10deg);
    -ms-transform: translate(-50%, -50%) rotate(-10deg);
  }
  20%, 40% {
    transform: translate(-50%, -50%) rotate(10deg);
    -webkit-transform: translate(-50%, -50%) rotate(10deg);
    -moz-transform: translate(-50%, -50%) rotate(10deg);
    -o-transform: translate(-50%, -50%) rotate(10deg);
    -ms-transform: translate(-50%, -50%) rotate(10deg);
  }
}